.row{
    color:white;
    margin-left:20px
}

.row-posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    scrollbar-width: none;
   
}

.row-posters::-webkit-scrollbar{
    display: none;
}
.row-poster{
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
    
}
.row-poster:hover{
    transform: scale(1.08);
    opacity:1;
}

.row-posterLarge{
    max-height: 250px;
}
.row-posterLarge:hover{
    transform: scale(1.09);
    opacity:1;
}



@media screen and (min-width: 768px) {
    .main-container {
        
        height: 500px;
        background-position: top;
    }
}